.messageConfig{
  width: 100%;
  height: 100%;
  display: flex;
  padding: 10px;
  &-left{
    flex: 1;
    background: white;
    &Top{
      width: 100%;
      height: 60px;
      padding: 0 10px;
    }
  }
  &-right{
    flex: 2;
    margin-left: 10px;
    background: white;
    padding: 10px;
    &Top{
      width: 100%;
      height: 60px;
      padding: 0 10px;
      font-size: 18px;
    }
    &Font{
      padding: 0 10px;
    }
    &Disabled{
      padding: 0 10px;
      color: rgba(0,0,0,0.3);
      margin-top: 15px;
    }
  }
  &-flex{
    display: flex;
    margin-top: 30px;
  }
  &-column{
    display: flex;
    flex-direction: column;
  }
  .ant-menu-sub.ant-menu-inline{
    background: white;
  }
}

@primary-color: #1890ff;